* {
  box-sizing: border-box; }

html, body, #app, #react-app {
  height: 100%;
  width: 300px;
  max-width: 100%;
  background: transparent;
  padding: 0;
  margin: 0;
  color: white;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end; }

#widget-closed {
  border: none;
  background: coral;
  width: 60px;
  height: 60px;
  max-width: 100%;
  max-height: 100%;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  cursor: pointer;
  transition: all .2s ease-in-out; }
  #widget-closed:hover {
    background: #ff5a1d; }

#widget-open {
  height: 100%;
  width: 300px;
  border-left: 1px solid gray;
  background: lightgray;
  display: flex;
  flex-direction: column;
  color: black; }
  #widget-open .row {
    display: flex;
    flex-direction: row;
    padding: 1rem; }
  #widget-open .header-row {
    font-size: 1.5rem;
    justify-content: space-between;
    border-bottom: 1px solid gray; }
    #widget-open .header-row .close-widget {
      cursor: pointer;
      transition: all .2s ease-in-out; }
      #widget-open .header-row .close-widget:hover {
        color: #4d4d4d; }
